@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR:400,700&display=swap);
#statusBarContainer {
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: 0;
}

#statusBar {
  border-radius: 0 5px 5px 0;
  background-color: #e74c3c;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  height: 5px;
  width: 100%;
}

header {
  width: 100%;
  height: 75px;
  background-color: white;
  position: fixed;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  opacity: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  -webkit-transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: opacity 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, opacity 0.3s;
  transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

header h1 {
  font-size: 1.5rem;
  text-align: center;
  line-height: 75px;
  color: #2c3e50;
}

/*

Atom One Dark by Daniel Gamage
Original One Dark Syntax theme from https://github.com/atom/one-dark-syntax

base:    #282c34
mono-1:  #abb2bf
mono-2:  #818896
mono-3:  #5c6370
hue-1:   #56b6c2
hue-2:   #61aeee
hue-3:   #c678dd
hue-4:   #98c379
hue-5:   #e06c75
hue-5-2: #be5046
hue-6:   #d19a66
hue-6-2: #e6c07b

*/

.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #abb2bf;
  background: #282c34;
}

.hljs-comment,
.hljs-quote {
  color: #5c6370;
  font-style: italic;
}

.hljs-doctag,
.hljs-keyword,
.hljs-formula {
  color: #c678dd;
}

.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
  color: #e06c75;
}

.hljs-literal {
  color: #56b6c2;
}

.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta-string {
  color: #98c379;
}

.hljs-built_in,
.hljs-class .hljs-title {
  color: #e6c07b;
}

.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
  color: #d19a66;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
  color: #61aeee;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-link {
  text-decoration: underline;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}
body {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 16px;
  font-weight: normal;
}

#postHeaderWrapper {
  width: 100%;
  min-width: 800px;
  height: 100vh;
  min-height: 300px;
  background-repeat: no-repeat;
  background-position: center, 0;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  text-align: center;
}

#postHeaderTitle {
  font-size: 3.2rem;
  font-weight: bold;
  text-align: center;
  margin: 20px 0;
  background-color: #e74c3c;
  padding: 3px 3px 10px;
}

#postHeaderHr {
  width: 50px;
  height: 2px;
  border: none;
  background-color: white;
}

@-webkit-keyframes textHover {
  from {
    color: white;
  }
  to {
    color: #f37263;
  }
}

@keyframes textHover {
  from {
    color: white;
  }
  to {
    color: #f37263;
  }
}

#postHeaderDetails {
  margin: 20px 0;
  font-style: italic;
}

#postHeaderCategory {
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
}

#postHeaderDay {
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  margin-left: 10px;
}

.headerTag + .headerTag {
  margin-left: 5px;
}

#postHeaderCategory:hover {
  -webkit-animation: textHover 1s;
          animation: textHover 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

#postBodyWrapper {
  min-width: 800px;
  max-width: 1200px;
  height: 5000px;
  padding: 100px 50px;
  margin: 0 auto;
  color: #2c3e50;
}

#postBodyWrapper ::-moz-selection {
  background-color: #f39c12;
  color: white;
}

#postBodyWrapper ::selection {
  background-color: #f39c12;
  color: white;
}

#postBodyWrapper a {
  color: #e74c3c;
  font-style: italic;
  font-weight: bold;
}

#postBodyWrapper b {
  font-style: italic;
  font-weight: bold;
}

#postBodyWrapper h2 {
  font-size: 2rem;
  font-weight: bold;
  margin: 0 0 50px;
  border-bottom: 3px solid #eee;
  color: #e74c3c;
  padding: 0 0 20px;
}

#postBodyWrapper h2::before {
  content: '#';
}

#postBodyWrapper blockquote {
  width: 600px;
  min-height: 100px;
  margin: 50px auto;
  padding: 10px;
  text-align: left;
  background-color: #f1f3f5;
  border-left: 5px solid #e74c3c;
  color: #495057;
}

#postBodyWrapper p {
  font-size: 1.3rem;
  line-height: 1.9rem;
  text-align: left;
}

#postBodyWrapper .hljs {
  width: 600px;
  margin: 50px auto;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.5);
}

